import { Input, InputProps } from 'antd';
import { SearchProps } from 'antd/es/input';
import React from 'react';

type Props = {
  headTitle: string;
  onSearch?: SearchProps['onSearch'];
  search?: InputProps;
  children?: React.ReactNode;
  className?: string;
};
const TableHead: React.FC<Props> = (props: Props) => {
  const { className, headTitle, onSearch, search, children } = props;
  return (
    <div className={`flex items-center mb-6 gap-6 ${className}`}>
      <div className="font-medium">{headTitle}</div>
      <div className="">
        <Input.Search
          placeholder="Input search text"
          allowClear
          style={{ width: 264, height: 32 }}
          onSearch={onSearch}
          {...search}
        />
      </div>
      {children}
    </div>
  );
};

export default TableHead;
