import { InputProps, Table, TablePaginationConfig, TableProps } from 'antd';
import IconArrowLeft from 'assets/Icon/IconArrowLeft';
import IconArrowRight from 'assets/Icon/IconArrowRight';
import React, { useMemo, useState } from 'react';
interface IProps extends TableProps {
  pagination: TablePaginationConfig;
  searchInput?: InputProps;
  tClassName?: string;
  searchComponent?: React.ReactNode;
  dataSource: any[];
  currentPage?: number;
  showPaging?: boolean;
}
const BaseTable: React.FC<IProps> = (props: IProps) => {
  const { tClassName, loading, columns, pagination, dataSource, searchComponent, currentPage, showPaging } = props;
  const [pageSize, setPageSize] = useState((pagination && pagination.pageSize) || 10);
  const position = showPaging ? 'bottomRight' : 'none';

  const totalPages = useMemo(() => {
    return pagination.total && pagination.pageSize && Math.ceil(pagination.total / pagination.pageSize);
  }, [pagination.total, pagination.pageSize]);

  const currentPageNumber = useMemo(() => {
    return currentPage || 1;
  }, [currentPage]);

  return (
    <div className={tClassName && tClassName + ' rounded-2xl bg-white'}>
      {searchComponent}
      <Table
        loading={loading}
        rowKey="id"
        columns={columns}
        dataSource={dataSource}
        scroll={{ x: 1500, y: 500 }}
        pagination={{
          pageSize: pageSize,
          current: currentPageNumber,
          onShowSizeChange: (_, size) => setPageSize(size),
          showTotal() {
            return (
              <div className="text-sm">
                Page {currentPageNumber} of {totalPages}
              </div>
            );
          },
          itemRender(page, type, element) {
            const FIRST_PAGE = 1;
            if (type === 'next')
              return <>{currentPageNumber === totalPages ? <IconArrowRight color="#949494" /> : <IconArrowRight />}</>;
            if (type === 'prev')
              return <>{currentPageNumber === FIRST_PAGE ? <IconArrowLeft color="#949494" /> : <IconArrowLeft />}</>;
          },
          position: [position],
          ...pagination
        }}
      />
    </div>
  );
};
export default BaseTable;
