import { CloseOutlined } from '@ant-design/icons';
import { Button, Divider, Drawer, DrawerProps, Form, Input, Select } from 'antd';
import { ICandidateForm, IListCandidate } from 'models/jobs';
import { useState } from 'react';
import { useTranslation } from 'react-i18next';

interface IProps extends DrawerProps {
  open?: boolean;
  setOpen(): void;
  onSubmit?(candidate: ICandidateForm): void;
  item?: IListCandidate;
  options?: any;
}

function DrawerHeader({ setOpen }: IProps) {
  return (
    <div className="p-6 text-xl w-full text-white bg-red h-20 flex items-center justify-between">
      <span>Esther Howard</span>
      <CloseOutlined className="cursor-pointer" onClick={() => setOpen()} />
    </div>
  );
}

function DetailDrawer(props: IProps) {
  const { t } = useTranslation();
  const { open, setOpen, onSubmit, item, options } = props;
  const [form] = Form.useForm();

  const [initialValues] = useState<ICandidateForm>({
    userStatus: item?.status,
    description: item?.fullName
  });

  const handleCancel = () => {
    setOpen();
    form.resetFields();
  };

  return (
    <Drawer
      width={448}
      className="ant-drawer-custom"
      title={<DrawerHeader setOpen={() => setOpen()} />}
      open={open}
      closable={false}
      afterOpenChange={() => form.resetFields()}
    >
      <Form
        layout="vertical"
        className="flex flex-col justify-between h-full"
        form={form}
        onFinish={onSubmit}
        initialValues={initialValues}
      >
        <div>
          <Form.Item label={t('common.label.status')} name="userStatus" rules={[{ required: true }]}>
            <Select placeholder={t('common.label.status')} options={options}></Select>
          </Form.Item>
          <Form.Item label={t('common.label.description')} name="description">
            <Input.TextArea showCount maxLength={100} rows={4} />
          </Form.Item>
        </div>

        <div className="h-full content-end">
          <Divider />
          <div className="flex items-center justify-between gap-4 ">
            <Button className="w-full h-10" onClick={handleCancel}>
              {t('common.label.cancel')}
            </Button>
            <Button className="w-full h-10" danger type="primary" onClick={() => form.submit()}>
              {t('common.label.save')}
            </Button>
          </div>
        </div>
      </Form>
    </Drawer>
  );
}

export default DetailDrawer;
