import { deleteRequest, getRequest, putRequest } from 'apis/services';
import { IPagingParams } from 'models/common';

interface IParamsCandidate extends IPagingParams {
  fullName?: string;
  status?: number;
}
function getListCandidate(id: number | string, params?: IParamsCandidate | any) {
  return getRequest({
    url: `/admin/candidates/job/${id}`,
    params: {
      limit: 999999,
      page: 1,
      ...params
    }
  });
}

function updateStatusCandidate(id: number | string, status: string) {
  return putRequest({
    url: `/admin/candidates/${id}`,
    payload: {
      status
    }
  });
}

function deleteCandidate(id: number | string) {
  return deleteRequest({
    url: `/admin/candidates/${id}`
  });
}

export { getListCandidate, updateStatusCandidate, deleteCandidate };
