import { Avatar, Button, Popconfirm, Select, Space, Spin } from 'antd';
import { ColumnType } from 'antd/es/table';
import IconPencil from 'assets/Icon/IconPencil';
import IconTrash from 'assets/Icon/IconTrash';
import JobForm from 'components/Form/JobForm';
import { ICandidateForm, IJobForm, IListCandidate } from 'models/jobs';

import { deleteCandidate, getListCandidate, updateStatusCandidate } from 'apis/modules/Candidates';
import { getCandidateStatus } from 'apis/modules/Common';
import { createJob, getJobById, updateJob } from 'apis/modules/Jobs';
import BaseTable from 'components/BaseTable';
import TableHead from 'components/BaseTable/TableHead';
import { CANDIDATE_STATUS } from 'constants/common';
import Routers from 'constants/routers';
import dayjs from 'dayjs';
import noti from 'libs/notification';
import React, { useCallback, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Link, useNavigate, useParams } from 'react-router-dom';
import { downloadLink } from 'utils/helpers';
import DetailDrawer from './component/DetailDrawer';

const JobDetail: React.FC = () => {
  const { t } = useTranslation();
  const navi = useNavigate();
  const { id } = useParams();
  const [dataSource, setDataSource] = useState<IListCandidate[]>([]);
  const [loading, setLoading] = useState(false);
  const [loadingCandidate, setLoadingCandidate] = useState(false);
  const [showDrawer, setShowDrawer] = useState(false);
  const [selectedItem, setSelectedItem] = useState<IListCandidate>();
  const [dataForm, setDataForm] = useState<IJobForm>();
  const [isProcess, setIsProcess] = useState(false);
  const [candidateStatus, setCandidateStatus] = useState([]);
  const [pagingCandidate, setPagingCandidate] = useState({
    total: 0,
    limit: 10,
    page: 1
  });
  const [searchParam, setSearchParam] = useState({
    fullName: undefined,
    status: undefined
  });

  const columns: ColumnType<IListCandidate>[] = [
    {
      title: t('common.label.fullName'),
      dataIndex: 'fullName',
      key: 'fullName',
      width: 250,
      render(value, record, index) {
        return (
          <Space size={8}>
            <Avatar />
            {record.fullName}
          </Space>
        );
      },
      fixed: true
    },
    {
      title: t('common.label.phoneNumber'),
      dataIndex: 'phoneNumber',
      key: 'phoneNumber'
    },
    {
      title: t('common.label.email'),
      dataIndex: 'email',
      key: 'email',
      width: 230
    },
    {
      title: t('common.label.referer'),
      dataIndex: 'referer',
      key: 'referer'
    },
    {
      title: t('common.label.status'),
      dataIndex: 'status',
      key: 'status',
      render(value, record, index) {
        return (
          <span className={record.status === CANDIDATE_STATUS.FAIL ? 'text-red' : 'text-green-600'}>
            {record.status}
          </span>
        );
      }
    },
    {
      title: t('common.label.resume'),
      dataIndex: 'resume',
      key: 'resume',
      render(value, record, index) {
        return (
          <div onClick={() => downloadLink(record.resume)} className="text-blue-600 hover:text-red cursor-pointer">
            {record.resume}
          </div>
        );
      }
    },

    {
      title: `${t('common.label.other')} ${t('common.label.resume')}`,
      dataIndex: 'otherAttachment',
      key: 'otherAttachment',
      render(value, record, index) {
        return (
          <div
            onClick={() => downloadLink(record.otherAttachment)}
            className="text-blue-600 hover:text-red cursor-pointer"
          >
            {record.otherAttachment}
          </div>
        );
      }
    },
    {
      title: t('common.label.portfolio'),
      dataIndex: 'portfolio',
      key: 'portfolio',
      render(value, record, index) {
        return (
          <Link to={'123'} className="text-blue-600 hover:text-red">
            {record.portfolio}
          </Link>
        );
      }
    },
    {
      title: t('common.label.action'),
      key: 'action',
      width: '150px',
      className: '!text-center',
      render: (_, record) => (
        <div className="h-full flex items-center justify-center table--col-action">
          <Button className="outline-none border-0" onClick={() => handleShowCandiate(record)}>
            <IconPencil />
          </Button>
          <Popconfirm
            title="Are you sure delete this candidate?"
            onConfirm={() => onConfirmDelete(record.id as any)}
            okText="Yes"
            cancelText="No"
            okButtonProps={{ danger: true }}
          >
            <Button className="outline-none border-0">
              <IconTrash />
            </Button>
          </Popconfirm>
        </div>
      )
    }
  ];

  const fetchDataCandidate = useCallback(async () => {
    try {
      setLoadingCandidate(true);
      const params = {
        limit: pagingCandidate.limit,
        page: pagingCandidate.page,
        ...searchParam
      };
      if (id) {
        const response: any = await getListCandidate(id, params);

        if (response && response.code === 'SUCCESS' && response.data) {
          setDataSource(response.data);
          setPagingCandidate((prev) => ({
            ...prev,
            total: response.data.total
          }));
        }
      }
    } catch (error) {
      noti('error', 'Failed to get data candidate');
    } finally {
      setLoadingCandidate(false);
    }
  }, [searchParam, pagingCandidate.page, pagingCandidate.limit]);

  const fetchData = async () => {
    try {
      setLoading(true);
      if (id) {
        const response = (await getJobById(id)) as any;
        if (response && response.code === 'SUCCESS' && response.data) {
          setDataForm(response.data);
        } else {
          noti('error', response.message);
        }
      }
    } catch (error) {
      noti('error', 'Failed to get data job detail');
    } finally {
      setLoading(false);
    }
  };

  const initPage = async () => {
    await getStatusOptions();
    await fetchData();
  };

  useEffect(() => {
    initPage();
  }, []);

  useEffect(() => {
    fetchDataCandidate();
  }, [searchParam, pagingCandidate.limit, pagingCandidate.page]);

  async function getStatusOptions() {
    try {
      const response = await getCandidateStatus();
      if (response && response.data) {
        const options = response.data.map((el: any) => ({ label: el.status, value: el.code }));
        setCandidateStatus(options);
      }
    } catch (error) {
      setCandidateStatus([]);
    }
  }

  const onSubmitUpdateJob = async (values: IJobForm, skills: string[]) => {
    try {
      setIsProcess(true);
      const payload = {
        // department: values.department,
        jobName: values.jobName,
        workingTime: values.workingTime,
        city: values.city,
        experience: values.experience,
        startDate: dayjs(values.startDate).format('YYYY-MM-DD'),
        endDate: dayjs(values.endDate).format('YYYY-MM-DD'),
        budget: values.budget && isNaN(values.budget) ? 0 : Number(values.budget),
        requirement: values.requirement,
        description: values.description,
        skills
      };
      const response: any = await updateJob(id, payload);
      if (response && response.code === 'SUCCESS') {
        noti('success', 'Update job successfully');
        navi(Routers.Jobs);
      } else {
        noti('error', response.message);
      }
    } catch (error) {
      noti('error', 'Failed to update job');
    } finally {
      setIsProcess(false);
    }
  };

  const onSearchCandidates = (value: number | string, key: string) => {
    setPagingCandidate((prev) => {
      return {
        ...prev,
        page: 1
      };
    });
    setSearchParam((prev) => {
      return {
        ...prev,
        [key]: value || undefined
      };
    });
  };

  const handleShowCandiate = (item: IListCandidate) => {
    setShowDrawer(true);
    setSelectedItem(item);
  };

  const handleSubmitCandidateForm = async (values: ICandidateForm) => {
    if (!selectedItem) return;
    try {
      setIsProcess(true);
      const response: any = await updateStatusCandidate(selectedItem?.id as number, values.userStatus as string);
      if (response && response.code === 'SUCCESS') {
        noti('success', 'Update candidate successfully');
        fetchDataCandidate();
        setShowDrawer(false);
      } else {
        noti('error', response.message);
      }
    } catch (error) {
      noti('error', 'Failed to update candidate');
    } finally {
      setIsProcess(false);
    }
  };

  const onConfirmDelete = async (id: string | number) => {
    try {
      const response: any = await deleteCandidate(id);
      if (response && response.code === 'SUCCESS') {
        noti('success', 'Delete successfully');
        fetchDataCandidate();
      } else {
        noti('error', response.message);
      }
    } catch (error: any) {
      noti('error', 'Failed to delete candidate');
    }
  };

  const onSubmitCopyJob = async (values: IJobForm, skills: string[]) => {
    try {
      setIsProcess(true);
      const payload = {
        jobName: `${values.jobName} Copy`,
        workingTime: values.workingTime,
        city: values.city,
        experience: values.experience,
        startDate: dayjs(values.startDate).format('YYYY-MM-DD'),
        endDate: dayjs(values.endDate).format('YYYY-MM-DD'),
        budget: values.budget && isNaN(values.budget) ? 0 : Number(values.budget),
        requirement: values.requirement,
        description: values.description,
        department: values.department,
        skills
      };
      const response: any = await createJob(payload);
      if (response && response.code === 'SUCCESS') {
        noti('success', 'Copy job successfully');
        navi(Routers.Jobs);
      } else {
        noti('error', response.message);
      }
    } catch (error) {
      noti('error', 'Failed to copy job');
    } finally {
      setIsProcess(false);
    }
  };

  const onSubmitFormJob = async (values: IJobForm, skills: string[], type?: string) => {
    if (type === 'duplicate') {
      onSubmitCopyJob(values, skills);
    } else {
      onSubmitUpdateJob(values, skills);
    }
  };

  return (
    <div className="h-full">
      <div className="h-16 bg-white w-full">
        <div className="flex h-full flex-row justify-between items-center px-6">
          <div className="text-xl font-bold">{t('page.jobs.titleDetail')}</div>
        </div>
      </div>
      <div className="my-3 mx-7 rounded-2xl p-4 bg-white">
        <Spin spinning={loading}>
          <JobForm isProcess={isProcess} item={dataForm} textButton="Save" onSubmit={onSubmitFormJob} />
        </Spin>
        <BaseTable
          loading={loadingCandidate}
          rowKey={(record) => `Candidate-${record.id}`}
          columns={columns}
          dataSource={dataSource}
          showPaging
          currentPage={pagingCandidate.page}
          searchComponent={
            <TableHead
              headTitle={t('page.jobs.label.listCandidate')}
              onSearch={(v) => onSearchCandidates(v, 'fullName')}
            >
              <Select
                placeholder="Select status"
                onChange={(v: number) => onSearchCandidates(v, 'status')}
                style={{
                  width: '220px'
                }}
                options={candidateStatus}
              ></Select>
            </TableHead>
          }
          pagination={{
            total: pagingCandidate.total,
            showSizeChanger: true,
            current: pagingCandidate.page,
            pageSize: pagingCandidate.limit,
            onShowSizeChange(current, size) {
              setPagingCandidate((prev) => {
                return {
                  ...prev,
                  limit: size
                };
              });
            },
            onChange(page) {
              setPagingCandidate((prev) => {
                return {
                  ...prev,
                  page
                };
              });
            }
          }}
        />
        {showDrawer && (
          <DetailDrawer
            item={selectedItem}
            open={showDrawer}
            options={candidateStatus}
            setOpen={() => setShowDrawer(!showDrawer)}
            onSubmit={handleSubmitCandidateForm}
          />
        )}
      </div>
    </div>
  );
};

export default JobDetail;
